/* GRID */
.Col-sm-1-2 {
  width: 100%;
}

@media (min-width: 480px) {
  .Row-sm {
    display: flex;
  }
  .Col-sm-1-2 {
    box-sizing: border-box;
    padding: 0 16px;
    width: 50%;
  }
  .Col-sm-1-2:nth-child(2n+1) {
    padding-left: 0;
  }
  .Col-sm-1-2:nth-child(2n+0) {
    padding-right: 0;
  }
  .Col-sm-1-4 {
    width: 25%;
  }
  .Col-sm-3-4 {
    width: 75%;
  }
}

.Col-md-1-2,
.Col-md-1-3,
.Col-md-1-4 {
  width: 100%;
}

@media (min-width: 768px) {
  .Row-md {
    display: flex;
  }
  .Col-md-1-2 {
    box-sizing: border-box;
    width: 50%;
  }
  .Col-md-1-2 {
    padding: 0 16px;
  }
  .Col-md-1-2:nth-child(2n+1),
  .Col-md-1-3:nth-child(2n+1),
  .Col-md-1-4:nth-child(2n+1) {
    padding-left: 0;
  }
  .Col-md-1-2:nth-child(2n+0),
  .Col-md-1-3:nth-child(2n+0),
  .Col-md-1-4:nth-child(2n+0) {
    padding-right: 0;
  }
  .Col-md-1-4 {
    box-sizing: border-box;
    padding: 0 2.5%;
    width: 25%;
  }
  .Col-md-3-4 {
    width: 75%;
  }
  .Col-md-1-3 {
    box-sizing: border-box;
    padding: 0 2.5%;
    width: 33.33%;
  }
  .Col-md-2-3 {
    width: 66.66%;
  }
}

.Row {
  display: flex;
}

.col-3-4 {
  flex: 0 0 75%;
}

.col-2-3 {
  flex: 0 0 66.66%;
}

.col-1-3 {
  box-sizing: border-box;
  padding: 0 8px !important;
  flex: 0 0 100%;
}

@media (min-width: 768px) {
  .col-1-3 {
    flex: 0 0 33.33%;
  }
}

